import React from 'react'
import { getSeasonFooter, getSeasonTitle } from '../../../lib/season'
import { OverlayProps } from '../../interface'
import './index.css'

const year = new Date().getFullYear()
const build = process.env.build

export function WelcomeNotice({ version, language, active }: OverlayProps) {
  if (!active) return null

  return (
    <div className="notice-welcome">
      <p> {getSeasonTitle(true) || 'Matcha'} </p>
      <p></p>
      <ol>
        <li>
          <span className="tag">ngld </span>
        </li>
        <li>
          <span className="tag">{getSeasonTitle() || ' Matcha'}</span>
        </li>
      </ol>
      <p>
        {version || '<>'} / {language}
      </p>
      <p className="copyright">
        &copy; {year} FFCafe / Build: {build}
        {getSeasonFooter()}
      </p>
    </div>
  )
}
